import React, { useEffect, useState } from "react";
import { Routes, Route } from "react-router-dom";
import { useQuery } from "./hooks/useQuery";
import Search from "./component/Search";
import Title from "./component/Title";
import Movies from "./component/Movies";
import Details from "./component/Details";
import CircularProgress from "@mui/material/CircularProgress";

function App() {
  const [movies, setMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [pagination, setPagination] = useState(1);
  const query = useQuery();
  const search = query.get("search");
  useEffect(() => {
    const apiMovies = async () => {
      const searchUrl = `https://api.themoviedb.org/3/search/movie?api_key=75edc4875e7f298ca391fb3ed4d5e711&query=${search}`;
      const api = await fetch(
        search
          ? searchUrl
            ? searchUrl
            : `https://api.themoviedb.org/3/movie/popular?api_key=75edc4875e7f298ca391fb3ed4d5e711&page=${pagination}`
          : `https://api.themoviedb.org/3/movie/popular?api_key=75edc4875e7f298ca391fb3ed4d5e711&page=${pagination}`
      );
      const data = await api.json();
      setIsLoading(false);
      setMovies(data.results);
    };
    apiMovies();
  }, [search, pagination]);

  const handleChange = (event, value) => {
    setPagination(value);
  };
  return (
    <>
      {isLoading ? (
        <div className="movies__loading">
          <CircularProgress />
        </div>
      ) : (
        <>
          <Title />
          <Search />
          <Routes>
            <Route
              exact="true"
              path="/App-Movies/"
              element={
                <Movies
                  movies={movies}
                  pagination={pagination}
                  handleChange={handleChange}
                />
              }
            />
            <Route path="/movies/:movieId" element={<Details />} />
          </Routes>
        </>
      )}
    </>
  );
}

export default App;
