import React, { useEffect, useState } from "react";
import notFound from "../images/placeholder.jpg";
import { useParams } from "react-router";
import { Button } from "@mui/material";
import { FiSend } from "react-icons/fi";
import "../styles/details.css";

const Details = () => {
  const { movieId } = useParams();
  const [details, setDetails] = useState([]);
  const [genre, setGenre] = useState([]);
  const [video, setVideo] = useState("");
  useEffect(() => {
    const apiMovieId = async () => {
      const api = await fetch(
        `https://api.themoviedb.org/3/movie/${movieId}?api_key=75edc4875e7f298ca391fb3ed4d5e711`
      );
      const apiVideos = await fetch(
        `https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=75edc4875e7f298ca391fb3ed4d5e711`
      );
      const data = await api.json();
      const urlVideo = await apiVideos.json();
      setDetails(data);
      setGenre(data.genres);
      setVideo(urlVideo.results);
    };
    apiMovieId();
  }, [movieId]);

  const genreList = genre.map((gen) => gen.name);
  let genreEnd = [" and"];
  genreEnd.push(genreList.pop());
  let str = genreList.join(", ");
  str += genreEnd.join(" ");

  return (
    <section className="container__detail">
      <main className="detail">
        <div className="detail__img">
          <img
            src={
              details.poster_path
                ? `https://image.tmdb.org/t/p/w500/${details.poster_path}`
                : notFound
            }
            alt={`${details.title}`}
          />
        </div>
        <article className="detail__content">
          <h1 className="content__title">{details.title}</h1>{" "}
          <p className="content__genres">
            <strong>Genres: </strong>
            {str}
          </p>{" "}
          <p className="content__release_date">
            <strong>Date:</strong> {details.release_date}
          </p>
          <p className="content__description">
            <strong>Description:</strong> {details.overview}
          </p>
          <Button
            className="content__video"
            href={
              video.length > 0
                ? `https://www.youtube.com/watch?v=${video[0].key}`
                : "https://www.youtube.com"
            }
            target="_blank"
            endIcon={<FiSend />}
          >
            Watch the trailer
          </Button>
          <p className="content__vote_average">
            <strong>Vote:</strong> {details.vote_average}/10
          </p>
        </article>
      </main>
    </section>
  );
};

export default Details;
