import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import Footer from "./Footer";
import notFound from "../images/placeholder.jpg";
import "../styles/movies.css";
import Empty from "./Empty";

const Movies = ({ movies, pagination, handleChange }) => {
  const [description, setDescription] = useState(-1);
  if (movies.length === 0) {
    return <Empty />;
  }
  return (
    <>
      <>
        <main className="container-movies">
          {movies.map((movie, index) => (
            <NavLink
              key={index}
              to={`/movies/${movie.id}`}
              onMouseEnter={() => setDescription(movie.id)}
            >
              <div className="movies__card">
                <img
                  src={
                    movie.poster_path
                      ? `https://image.tmdb.org/t/p/w500${movie.poster_path}`
                      : notFound
                  }
                  alt={`${movie.title}`}
                />
                <section
                  className={
                    movie.id === description
                      ? `movies__description showDescription`
                      : "movies__description"
                  }
                >
                  <div className="description__content">
                    <h1 className="content__title">{movie.title}</h1>
                  </div>
                </section>
              </div>
            </NavLink>
          ))}
        </main>
        <Footer
          movies={movies}
          handleChange={handleChange}
          pagination={pagination}
        />
      </>
    </>
  );
};

export default Movies;
