import React from "react";
import { NavLink } from "react-router-dom";
import "../styles/title.css";

const Title = () => {
  return (
    <div className="title">
      <h1>
        <NavLink exact="true" to="/App-Movies/">
          Movies
        </NavLink>
      </h1>
    </div>
  );
};

export default Title;
